body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #4B2D73;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.bg-color--1 {
	background-color: #1C053A;
}

.bg-color--2 {
	background-color: #311557;
}

.bg-color--3 {
	background-color: #4B2D73;
}

.bg-color--4 {
	background-color: #6B4E90;
}

.bg-color--5 {
	background-color: #8F78AD;	
}

.bg-color--empty {
	background-color: rgba(0,0,0,0);
}

.outline--none {
	outline: none;
}

.bg-color--inherit {
	background-color: inherit;
}

.hover--2:hover {
	background-color: #311557;
}

.border--1 {
	border-radius: 1px;
}

.border-color--2 {
	border-color: #1C053A;
}
.Play {
	position: relative;
}

.Tile {
	width: 25%;
	height: 25%;
}

.Play__timer {
	position: absolute;
	z-index: 2;
	top: 0;
	width: 100%;
}

button {
	color: #bbbbbb;
}
.ViewLayer {
	width: 256px;
	height: 512px;
}

@media only screen and (min-width: 768px) {
	.ViewLayer {
		width: 325px;
		height: 650px;
	}
} 
